<template lang="pug">
#Mode.bg-white.pa1
    div(v-for="([mode, caption]) in modes")
        input.ma1.dib#none(:id="mode" type="radio" :value="mode" v-model="selectedMode") 
        label.pa1-ns.dib(:for="mode") {{ caption }}

</template>

<script>
import { EventBus } from '../EventBus'
export default {
    name: "Mode",
    data: () => ({
        selectedMode: 'none',
        modes: [
                ['none', 'No vis.'],
                ['species', 'Species'],
                ['rarity', 'Rarity.'],
                ['harm', 'Harm'],
                ['local', 'Local variety'],
                ['trunk', 'Trunk size'],

        ]
    }),
    created() {
        window.Mode = this;
    },
    watch: {
        selectedMode() {
            EventBus.$emit('vis-mode', this.selectedMode);
        }
    }
}
</script>

